@import "components/rules/rules.css";
@import "components/form/form.css";
@import "components/cards/cards.css";
@import "components/popup/popup.css";

* {
	margin: 0;
	padding: 0;
}

body {
	width: 100%;
	height: 100%;
  line-height: 1;
  font-family: Arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #000;
	background-image: -webkit-gradient(linear, left top, right top, from(#A9A9A9), color-stop(#D3D3D3), to(#F5F5F5));
	background-image: -webkit-linear-gradient(left, #A9A9A9, #D3D3D3, #F5F5F5);
	background-image: -o-linear-gradient(left, #A9A9A9, #D3D3D3, #F5F5F5);
	background-image: linear-gradient(to right, #A9A9A9, #D3D3D3, #F5F5F5);
}



